async function post(endpoint, data) {
  const user = await Auth.currentAuthenticatedUser()

  const token = user.signInUserSession.idToken.jwtToken

  const requestInfo = {
    headers: {
      Authorization: token
    },
    body: data
  }
  const response = await API.post('reactrestauthapi', endpoint, requestInfo)
  console.log(response)
}

async function get(endpoint) {
  const user = await Auth.currentAuthenticatedUser()

  const token = user.signInUserSession.idToken.jwtToken

  const requestInfo = {
    headers: {
      Authorization: token
    }
  }
  const data = await API.get('reactrestauthapi', endpoint, requestInfo)

  console.log({ data })
}

